<template>

    <v-bottom-sheet v-model="sheet" inset>
                        <template v-slot:activator="{ on, attrs }">
                                <v-sheet class="d-flex justify-center align-center flex-column select" height="100%" v-bind="attrs" v-on="on">
                                    <v-btn class="mx-2" fab dark color="red">
                                        <v-icon dark>mdi-plus</v-icon>
                                    </v-btn>
                                    <br>
                                    <p class="text-center">Nuevo usuario</p>
                                </v-sheet>
                        </template>
                        <v-sheet height="500px">
                                        <v-sheet height="100%" class="d-flex justify-center align-center">

                                            <v-sheet  height="95%" width="95%">
                                              <v-form ref="form_personal">
                                                <v-container fluid>
                                                  <v-row no-gutters>
                                                    <v-col cols="12">
                                                      <h2 class="primary--text">Registrar nuevo usuario</h2>
                                                      <br>
                                                    </v-col>
                                                    <v-col cols="12">
                                                      <v-text-field label="Nombre" required v-model="data.nombre"></v-text-field>
                                                    </v-col>
                                                    <v-col cols="6">
                                                      <v-text-field label="Apellido Paterno" required v-model="data.apellido_paterno"></v-text-field>
                                                    </v-col>
                                                    <v-col cols="6">
                                                      <v-text-field label="Apellido Materno" class="pl-10" required v-model="data.apellido_materno"></v-text-field>
                                                    </v-col>
                                                    <v-col cols="3">
                                                      <v-text-field label="Edad" required v-model="data.edad"></v-text-field>
                                                    </v-col>

                                                    <v-col cols="3">
                                                      <v-select :items="roles" label="Rol" class="pl-10" required v-model="data.rol"> </v-select>
                                                    </v-col>

                                                    <v-col cols="6">
                                                      <v-text-field label="Telefono" class="pl-10" required v-model="data.telefono"></v-text-field>
                                                      <br>
                                                    </v-col>

                                                    <v-col cols="6">
                                                      <v-text-field label="Email" required v-model="data.email"></v-text-field>
                                                      <br>
                                                    </v-col>

                                                    <v-col cols="6">
                                                      <v-text-field label="Contrase√±a" class="pl-10" required v-model="data.contrase√±a"></v-text-field>
                                                      <br>
                                                    </v-col>

                                                    <v-col cols="12">
                                                  <v-sheet class="d-flex align-center">
                                                    <v-btn color="primary" @click="saveitem">
