                                            </v-sheet>

                                          </v-sheet>
                        </v-sheet>
    </v-bottom-sheet>
</template>
<script>
import {  mapMutations  } from 'vuex'

export default {
    name: 'registrarpersonal',
    data: () => ({
        sheet: false,
        roles: ['Docente','Administrativo'],
        data: {
          nombre: '',
          apellido_paterno: '',
          apellido_materno: '',
          edad: 0,
          telefono: 0,
          email: '',
          contrase√±a:  '',
          rol: ''
        }

    }),
    methods:{
      saveitem(){
        this.additemstorage({
            prop: 'personal',
            value: this.data
        })
        this.sheet = !this.sheet
      },
      ...mapMutations(['additemstorage'])
    }
}
</script>