<template>
  <v-bottom-sheet v-model="sheet" inset>
      
          <template v-slot:activator="{ on, attrs }">
            <v-sheet class="d-flex justify-space-around align-center select" height="100%" v-bind="attrs" v-on="on">
                <p class="text-center">Gestionar anuncios</p>
                <v-btn class="mx-2" fab dark color="indigo">
                <v-icon dark>mdi-bell-ring</v-icon>
                </v-btn>
            </v-sheet> 
          </template>

          <v-sheet height="500px" class="pa-5">
              <v-form ref="formanuncios">
                    <v-container fluid>
                        <v-row no-gutters>
                            

                                <v-col cols="12">
                                    <v-tabs v-model="tab">
                                        <v-tab>anuncios</v-tab>
                                        <v-tab>crear nuevo anuncio</v-tab>
                                    </v-tabs>

                                    <v-tabs-items v-model="tab">
                                        <v-tab-item>
                                            <v-row>
                                                <v-col cols="12">
                                                    <v-sheet height="320" class="overflow-y-auto">
                                                        <v-alert dismissible border="left" colored-border color="blue" elevation="1" dense v-for="(item,key) in getanuncios" :key="key">
                                                            {{item.mensaje}}
                                                        </v-alert>    
                                                    </v-sheet>  
                                                </v-col>

                                                <v-col cols="12">
                                                    <v-btn class="ml-5" color="indigo" text @click="sheet = !sheet">Cancelar</v-btn>
                                                </v-col>
                                            </v-row> 
                                        </v-tab-item>

                                        <v-tab-item>
                                            <v-form ref="formanuncio">
                                                <v-row>
                                                    
                                                    <v-col cols="12">
                                                        <v-text-field :rules="rules.requerido" label="Mensaje" required v-model="data.mensaje"></v-text-field>
                                                    </v-col>
                                                    <v-col cols="6">
                                                            <v-select
                                                            :items="['todos','docentes','administrativos','vigilantes']"
                                                            label="Destinatario"
                                                            v-model="data.destinatario"
                                                            :rules="rules.requerido"
                                                            ></v-select>
                                                    </v-col>

                                                    <v-col cols="6">
                                                        <v-menu
                                                                ref="menu"
                                                                v-model="menu"
                                                                :close-on-content-click="false"
                                                                :return-value.sync="date"
                                                                transition="scale-transition"
                                                                offset-y
                                                                min-width="290px"
                                                            >
                                                                <template v-slot:activator="{ on, attrs }">
                                                                <v-text-field
                                                                    label="Duracion"
                                                                    readonly
                                                                    v-bind="attrs"
                                                                    v-on="on"
                                                                    v-model="date"
                                                                    :rules="rules.requerido"
                                                                ></v-text-field>
                                                                </template>
                                                                <v-date-picker
                                                                no-title
                                                                scrollable
                                                                range
                                                                v-model="date"
                                                                >
                                                                    <v-spacer></v-spacer>
                                                                    <v-btn
                                                                        text
                                                                        color="primary"
                                                                        @click="menu = false"
                                                                    >
                                                                        Cancelar
                                                                    </v-btn>
                                                                    <v-btn
                                                                        text
                                                                        color="primary"
                                                                        @click="$refs.menu.save(date)"
                                                                    >
                                                                        Aceptar
                                                                    </v-btn>
                                                                </v-date-picker>
                                                            </v-menu>
                                                        </v-col>

                                                <v-col cols="12">
                                                    <v-sheet class="d-flex align-center">
                                                        <v-btn color="primary" @click="onsubmit">
                                                        Crear
                                                        </v-btn>
                                                        <v-btn class="ml-5" color="indigo" text @click="sheet = !sheet">Cancelar</v-btn>
                                                    </v-sheet>

                                                </v-col>
                                                        
                                                </v-row>
                                            </v-form>
                                        </v-tab-item>
                                    </v-tabs-items>

                                </v-col>
                                
                                
                                
                        </v-row>
                    </v-container>
                </v-form>
    
          </v-sheet>
       </v-bottom-sheet>
</template>
<script>
import {  mapGetters, mapActions, mapMutations } from 'vuex'
// Relevante, Importante, Advertencia
export default {
    name: 'anuncios',
    data: () => ({
        sheet: false,
        tab: null,
        date: null,
        menu: false,
        data: {
            mensaje:'',
            destinatario: '',
            inicia: '',
            expira: ''
        },
        rules: {
            requerido: [v => !!v || 'Campo obligatorio'],
            email: [v => /.+@.+\..+/.test(v) || 'Email invalido']
        }
    }),
    mounted() {
       
    },
    methods: {
        ...mapActions(['doaddanuncio']),
        ...mapMutations(['message']),
        onsubmit(){
           
            if(this.$refs.formanuncio.validate()){
                this.message('Anuncio creado')
               this.data.inicia = this.date[0]
               this.data.expira = this.date[1]
               this.sheet = !this.sheet
               this.doaddanuncio(this.data)
               this.data = {}
            }
        }

    },
    computed: {
      ...mapGetters(['getanuncios'])

    }
    
}
</script>