<template>
    <v-sheet height="100%">
        
            <v-row style="height:50%;">
                <v-col cols="3">
                    <v-card height="100%" class="v-step-0" id="v-step-0" @click="add">
                       <v-card-title>Total de equipo inventariado</v-card-title>
                        <apexchart type="donut" :options="charts.donutoptions" :series="charts.donutseries"></apexchart>
                    </v-card>
                </v-col>

                <v-col cols="3">
                    <v-card height="100%">
                        <v-card-title height="20%">Equipo mas utilizado </v-card-title>
                        <apexchart height="80%" type="area" :options="charts.lineoptions" :series="charts.lineseries"></apexchart>
                    </v-card>
                </v-col>

                <v-col cols="3">
                    <v-row style="height:100%;">
                        <v-col cols="12">
                            <v-card height="100%" link to="/administrador/personal">
                                <v-sheet class="d-flex justify-space-around align-center select" height="100%">
                                    <p class="text-center">Administrar Usuarios</p>
                                    <v-btn class="mx-2" fab dark color="indigo">
                                        <v-icon dark>mdi-account-group</v-icon>
                                    </v-btn>
                                </v-sheet>
                            </v-card>
                        </v-col>
                        <v-col cols="12">
                            <v-card height="100%" link to="/administrador/equipo">
                                <v-sheet class="d-flex justify-space-around align-center select" height="100%">
                                    <p class="text-center">Administrar Equipo</p>
                                    <v-btn class="mx-2" fab dark color="indigo">
                                        <v-icon dark>mdi-laptop-mac</v-icon>
                                    </v-btn>
                                </v-sheet>
                            </v-card>
                        </v-col>
                        <v-col cols="12">
                            <v-card height="100%"> 
                                            <anuncios/>              
                                          
                            </v-card>
                        </v-col>                      
                    </v-row>
                </v-col>
                <v-col cols="3">
                    <v-row style="height:100%;">
                        <v-col cols="6">
                            <v-card height="100%">
                                <registrarequipo/>
                            </v-card>
                        </v-col>
                        <v-col cols="6">
                            <v-card height="100%">
                                <registrarpersonal/>
                            </v-card>
                        </v-col>
                        <v-col cols="6">
                            <v-card height="100%"> 
                                <generarcodigoqr/>                       
                            </v-card>
                        </v-col>
                        <v-col cols="6">
                            <v-card height="100%">  
                                <departamentos/>                               
                            </v-card>
                        </v-col>                       
                    </v-row>
                </v-col>
            </v-row>

        <v-row style="height:50%;">
                <v-col cols="3">
                    <v-row style="height:100%;">
                        <v-col cols="6">
                            <v-card height="100%">
                                <v-sheet class="d-flex justify-center align-center flex-column" height="100%">
                                    <h1> {{ counter.equipo }} </h1>
                                    <br>
                                    <p class="text-center">Equipo Registrado</p>
                                </v-sheet>
                            </v-card>
                        </v-col>
                        <v-col cols="6">
                            <v-card height="100%">
                                <v-sheet class="d-flex justify-center align-center flex-column" height="100%">
                                    <h1> {{ counter.personal }} </h1>
                                    <br>
                                    <p class="text-center">Usuarios registrados</p>
                                </v-sheet>
                            </v-card>
                        </v-col>
                        <v-col cols="6">
                            <v-card height="100%">   
                                <v-sheet class="d-flex justify-center align-center flex-column" height="100%">
                                    <h1> {{ counter.dentro }} </h1>
                                    <br>
                                    Equipo
                                    <p class="text-center">dentro de almacen</p>
                                </v-sheet>                                                     
                            </v-card>
                        </v-col>
                        <v-col cols="6">
                            <v-card height="100%">   
                                <v-sheet class="d-flex justify-center align-center flex-column" height="100%">
                                    <h1> {{ counter.fuera }} </h1>
                                    <br>
                                    Equipo
                                    <p class="text-center">fuera de almacen</p>
                                </v-sheet>                      
                            </v-card>
                        </v-col>                       
                    </v-row>
                </v-col>

                <v-col cols="9">
                    <v-card height="100%">
                        <v-card-title>Frecuencia de entrada/salida</v-card-title>
                        <apexchart height="80%" type="bar" :options="charts.options" :series="charts.series" ref="grafica"></apexchart>
                    </v-card>
                </v-col>
        </v-row>

        <v-tour name="administradordashboard" :steps="steps"></v-tour>
    </v-sheet>
</template>
<script>
import registrarpersonal from '@/views/administrador/registrarpersonal.vue'
import registrarequipo from '@/views/administrador/registrarequipo.vue'
import generarcodigoqr from '@/views/administrador/generarcodigoqr.vue'
import departamentos from '@/views/administrador/departamentos.vue'
import anuncios from '@/views/administrador/anuncios.vue'
import {  mapActions , mapMutations, mapGetters  } from 'vuex'

export default {
  name: 'dashboard',
  components: {
      registrarpersonal,
      registrarequipo,
      generarcodigoqr,
      departamentos,
      anuncios
  },
  async mounted() {
        await this.doinit()
        this.initcounters()
      
  },
  created: function () {

  },
  data: () => ({
    counter: {
        equipo: 0,
        personal: 0,
        dentro: 0,
        fuera: 0
    },
    charts: {
        donutoptions:{
            legend:{
                show: false
            },
            labels: ['Dentro de almacen', 'Fuera de almacen']
        },
        donutseries: [2, 8],
        options: {
            chart: {
            id: 'frecuencia-uso'
            },
            xaxis: {
            categories: ['ene', 'feb', 'mar','abr','may','jun','jul','ago','sep','oct','nov','dic']
            }
        },
        series: [{
            name: 'Equipo usado',
            data: [30, 40, 45, 50, 49, 60, 70, 91]
        }],
        lineoptions: {
            chart: {
                id: 'frecuencia-uso',
                toolbar: {
                    show: false
                    }
            },
            xaxis: {
                categories: ['laptop', 'monitor', 'mouse']
            }
        },
        lineseries: [{
            name: 'Equipo usado',
            data: [30, 40, 45]
        }]

    },
    steps: [
          {
            target: '#v-step-0',  
            content: `Discover <strong>Vue Tour</strong>!`
          }
    ]

  }),
  methods: {
    ...mapActions(['doinit']),
    ...mapMutations(['additemstorage']),
    initcounters(){
        this.counter.equipo =  this.getitems('equipo').length 
        this.counter.personal =  this.getitems('personal').length 
        this.counter.fuera =  this.getitems('salida_equipo').length 
        this.counter.dentro =  this.counter.equipo - this.counter.fuera
    },
    add(){
        this.additemstorage({
            prop: 'personal',
            value: 90
        })
    }
  },
  computed: {
    ...mapGetters(['getitems'])
    
  }
}
</script>