<template>

    <v-bottom-sheet v-model="sheet" inset>
                        <template v-slot:activator="{ on, attrs }">
                                <v-sheet class="d-flex justify-center align-center flex-column select" height="100%" v-bind="attrs" v-on="on" @click="open">
                                    <v-btn class="mx-2" fab dark color="red">
                                        <v-icon dark>mdi-plus</v-icon>
                                    </v-btn>
                                    <br>
                                    <p class="text-center">Nuevo equipo</p>
                                </v-sheet>
                        </template>
                        <v-sheet height="650px" class="d-flex justify-center align-center">
                            <v-sheet  height="95%" width="98%">
                                <v-form>
                                    <v-container fluid>
                                        <v-row no-gutters>
                                            <v-col cols="12">
                                                <h2 class="primary--text">Registrar nuevo equipo</h2>
                                            </v-col>

                                            <v-col cols="12">
                                                <v-text-field label="Nombre del equipo" required v-model="data.nombre"></v-text-field>
                                            </v-col>
                                            
                                            <v-col cols="4">
                                                      <v-text-field label="Marca" required v-model="data.marca"></v-text-field>
                                            </v-col>

                                            
                                            <v-col cols="4">
                                                      <v-text-field label="Numero de serie" class="ml-5" required v-model="data.no_serie"></v-text-field>
                                            </v-col>

                                            
                                            <v-col cols="4">
                                                      <v-text-field label="Modelo" class="ml-5" required v-model="data.modelo"></v-text-field>
                                            </v-col>

                                            
                                            <v-col cols="6">
                                                      <v-select :items="personal" label="Resguardo" required v-model="selecpersonal"> </v-select>
                                            </v-col>

                                            
                                            <v-col cols="6">
                                                    <v-select :items="tipo" label="Tipo" required v-model="selectipo" class="ml-5"> </v-select>

                                            </v-col>

                                            <v-col cols="6">
                                                <v-sheet>


                                                  <v-tabs v-model="tab">
                                                      <v-tab>Seleccionar imagen</v-tab>
                                                      <v-tab>Seleccionar imagen predeterminada</v-tab>
                                                  </v-tabs>

                                                  <v-tabs-items v-model="tab">
                                                      <v-tab-item>
                                                        <v-sheet height="200">
                                                          <v-img height="80%" :src="image" contain class="mt-2">

                                                          </v-img>
                                                          <v-file-input
                                                                accept="image/*"
                                                                prepend-icon="mdi-image"
                                                                @change="loadimage"
                                                                label="seleccionar"
                                                          ></v-file-input>
                                                        </v-sheet>
                                                      </v-tab-item>

                                                      <v-tab-item>
                                                        <v-sheet height="200">
                                                         <v-slide-group
                                                          v-model="model"
                                                          active-class="success"
                                                          show-arrows
                                                        >

                                                            <v-slide-item
                                                              v-for="image in slide"
                                                              :key="image"
                                                              v-slot="{ active, toggle }"
                                                            >
                                                              <v-card
                                                                class="ma-4"
                                                                height="150"
                                                                width="150"
                                                                @click="toggle"
                                                              >
                                                                
                                                                <v-img :src="image">
                                                                
                                                                  <v-fade-transition>
                                                                    <v-sheet color="rgba(0,0,0,0.6)" height="100%" width="100%" v-if="active" class="d-flex justify-center align-center">
                                                                    <v-icon
                                                                      v-if="active"
                                                                      color="white"
                                                                      size="48"
                                                                      v-text="'mdi-check'"
                                                                      large
                                                                    ></v-icon>
                                                                    </v-sheet>
                                                                  </v-fade-transition>
                                                                  
                                                                </v-img>
                                                              
                                                              </v-card>
                                                            </v-slide-item>
                                                          </v-slide-group>
                                                        </v-sheet>
                                                      </v-tab-item>
                                                  </v-tabs-items>

                                              
                                                  </v-sheet>
                                                
                                                <br>
                                            </v-col>

                                            <v-col cols="6">
                                                <v-sheet height="200" class="ml-5">

                                                  <v-row no-gutters>
                                                    <v-col cols="12">
                                                    <p>Propiedades</p>

                                                    </v-col>
                                                    <v-col cols="5">
                                                      <v-text-field label="Propiedad" required dense v-model="item.property"></v-text-field>
                                                    </v-col>
                                                    <v-col cols="5">
                                                      <v-text-field label="Valor" required dense class="ml-5" v-model="item.value"></v-text-field>
                                                    </v-col>
                                                    <v-col cols="2">
                                                      <v-btn class="mx-2 ml-5" fab x-small color="primary" @click="addproperty">
                                                        <v-icon dark>mdi-plus </v-icon>
                                                      </v-btn>
                                                    </v-col>

                                                    <v-col cols="12">
                                                      
                                                        <v-chip color="primary" class="ma-2" v-for="(item, key) in properties" :key="key" close @click:close="eliminar(key)">
                                                            <strong > {{ Object.keys(item)[0] }}</strong>&nbsp;
                                                            <div class="text-body-1">({{ Object.values(item)[0] }})</div>
                                                        </v-chip>

                                                    </v-col>
                                                  </v-row>                                      
                                                </v-sheet>
                                            </v-col>

                                            <v-col cols="12">
                                                  <v-sheet class="d-flex align-center">
                                                    <v-btn color="primary" @click="save">
                                                      Registrar
                                                    </v-btn>
                                                    <a class="ml-5" @click="sheet = !sheet">Cancelar</a>
                                                  </v-sheet>
                                            </v-col> 



                                        </v-row>
                                    </v-container>
                                </v-form>
                            </v-sheet>
                        
                        


                        </v-sheet>
    </v-bottom-sheet>
</template>
<script>
import {  mapMutations ,  mapGetters} from 'vuex'
export default {
    name: 'registrarequipo',
    data: () => ({
        sheet: false,
        model: null,
        tab: null,
        image: null,
        item: {
          property: '',
          value: ''
        },
        properties: 
          [
            { ram: '6gb' }, { cpu: 'Intel Celeron' }
          ],
        slide: [
          'http://localhost/icons/001-ssd.png',
          'http://localhost/icons/007-ram.png',
          'http://localhost/icons/008-fan.png',
          'http://localhost/icons/009-disk.png',
          'http://localhost/icons/012-harddrive.png',
          'http://localhost/icons/013-controller.png',
          'http://localhost/icons/014-mouse.png',
          'http://localhost/icons/015-keyboard.png',
          'http://localhost/icons/016-pc.png'
        ],
        data: {
          nombre: '',
          marca: '',
          no_serie: '',
          modelo: '',
          foto: '',
          etiquetas: '',
          id_personal: '',
          id_tipo: ''
        },
        personal: [],
        tipo: [],
        selectipo: '',
        selecpersonal: ''

    }),
    methods: {
      addproperty() {
        let property={}
        property[this.item.property]=this.item.value
        this.properties.push(property)

        this.item.property=''
        this.item.value=''
      },
      loadimage(image){

          this.image=URL.createObjectURL(image)

      },
      eliminar(key){

        this.properties.splice(key,1)


      },
      save(){


        let etiquetas={}
        this.properties.forEach(item => {
          etiquetas[Object.keys(item)[0]] =  Object.values(item)[0] 
        });
        this.data.etiquetas = JSON.stringify(etiquetas)

        if(this.image){
          this.data.foto=this.image
        }else{
          this.data.foto= this.slide[this.model]
        }

        
        this.getpersonal.forEach(item => {

          if(item.nombre == this.selecpersonal){
            this.data.id_personal = item.id
          }

        
        });

        this.gettipo.forEach(item => {
          if(item.nombre == this.selectipo){
            this.data.id_tipo = item.id
          }
        });
        

        this.additemstorage({
            prop: 'equipo',
            value: this.data
        }) 

        this.sheet = !this.sheet

      },
      open(){

        this.getpersonal.forEach(element => {
          this.personal.push(element.nombre)
        });

        this.gettipo.forEach(element => {
          this.tipo.push(element.nombre)
        });

      },

      ...mapMutations(['additemstorage'])
    },
    computed: {
      ...mapGetters(['gettipo']),
      ...mapGetters(['getpersonal'])
    }

}
</script>